
html, body {
    margin: 0;
    
    padding: 0;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:after, *:before {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}
:root {
    --P_PageSection--dragged_height: 40px;
    --P_PageSection_sorting-transition-duration: 0.35s;
    --P_PageSection_sorting-transition-easing: ease-out;
    --P_PageSection--placeholder_text: "Um√≠stit sekci zde...";
}
.P_PageSection--placeholder {
    position: relative;
    
    height: var(--P_PageSection--dragged_height);
    width: 100%;

    color: #888;
    outline: 2px dashed currentColor;
    outline-offset: -4px;
}

.P_PageSection--placeholder:after {
    content: var(--P_PageSection--placeholder_text);
    
    font-size: 17px;
    
    position: absolute;
    top: 0px;
    left: 0px;
    
    width: 100%;
    height: 100%;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.P_PageSection--placeholder__transitions {
    height: var(--P_PageSection--dragged_height);
    width: 100%;
    
    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.P_PageSection {
    position: relative;
    
    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.P_PageSection--placed {
    z-index: 100;

    overflow: hidden;
}

.P_PageSection--dragged {
    z-index: 100;

    height: var(--P_PageSection--dragged_height);
    min-height: var(--P_PageSection--dragged_height);
    width: 100%;

    overflow: hidden;
}

.E .P_PageSection:after {
    content: attr(data-section-name);

    font-size: 19px;
    
    z-index: 100;
    position: absolute;
    top: 0px;
    left: 0px;
    
    width: 100%;
    height: 100%;
    
    color: #fff;
    background-color: #404040;
    opacity: 0;
    visibility: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
    
    transition-property: opacity, visibility;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.E .P_PageSection--dragged:after {
    opacity: 1;
    visibility: visible;
}